document.addEventListener( 'DOMContentLoaded', function() {
    let WebGoTo;
    let clHidden = 'hidden-element'

    function startAll(){
        WebGoTo = new WFSGoTo({
            elements: '.btn-scroll',
            duration: 1000,
            indicatorGoTop: 'go-to-top',
            showGoToTop: 'top',
            typePath: '#',
            spacebeforeElement: 100,
            mibileSpacebeforeElement: 0
        });
    }

    startAll();

    let linkDestroy = document.querySelector('.wfs-destroy');
    let linkStart = document.querySelector('.wfs-start');

    linkDestroy.addEventListener('click',  (event) => {
        event.preventDefault();
        WebGoTo.destroy();
        if(!linkDestroy.classList.contains(clHidden)) linkDestroy.classList.add(clHidden);
        if(linkStart.classList.contains(clHidden)) linkStart.classList.remove(clHidden);
    });

    linkStart.addEventListener('click',  (event) => {
        event.preventDefault();
        startAll();
        if(!linkStart.classList.contains(clHidden)) linkStart.classList.add(clHidden);
        if(linkDestroy.classList.contains(clHidden)) linkDestroy.classList.remove(clHidden);
    });
});
